import { RefObject } from 'preact';
import { useState } from 'preact/hooks';
import { useResizeObserver } from '../PRIVATE_useResizeObserver';

const useSize = (
  ref: RefObject<Element>,
  resizeObserverOptions: ResizeObserverOptions = { box: 'content-box' }
) => {
  const [size, setSize] = useState<{ width: number; height: number }>();

  useResizeObserver(
    ref,
    (entry) => {
      const boxSize =
        resizeObserverOptions.box === 'border-box' ? entry.borderBoxSize : entry.contentBoxSize;
      const newWidth = boxSize[0].inlineSize;
      const newHeight = boxSize[0].blockSize;
      setSize({ width: newWidth, height: newHeight });
    },
    resizeObserverOptions
  );

  return size;
};

export { useSize };
